import * as okayapi from '../../../components/okayapi/okayapi.js';


export default {
	data() {
		return {
			title: 'Music',
			music: [],
			input: '',
			page: 1,
			filter: 'name',
			website: 'netease',
			array: [{
					name: '网易音乐',
					value: 'netease'
				},
				{
					name: 'qq音乐',
					value: 'qq'
				},
				{
					name: '酷狗音乐',
					value: 'kuguo'
				},
				{
					name: '酷我音乐',
					value: 'kuwo'
				},
				{
					name: '百度音乐',
					value: 'baidu'
				},
				{
					name: '虾米音乐',
					value: 'xiami'
				},
				{
					name: '一听音乐',
					value: 'yiting'
				},
				{
					name: '咪咕音乐',
					value: 'migu'
				},
			],
			arrays: [
				'网易音乐',
				'qq音乐',
				'酷狗音乐',
				'酷我音乐',
				'百度音乐',
				'虾米音乐',
				'一听音乐',
				'咪咕音乐',
			],
			index: 0,
		}
	},
	methods: {
		searchOkay: function() {
			this.music = []
			this.page = 1
			uni.showLoading({
				title: '加载中'
			})
			var params = {
				s: "App.Music.Search", // 必须，待请求的接口服务名称
				input: this.input,
				filter: this.filter,
				website: this.website,
				page: this.page,
			};
			uni.request({
				url: this.okayapiHost,
				method: 'GET',
				data: okayapi.enryptData(params),
				success: res => {
					this.music = res.data.data.music
					this.page++
					uni.hideLoading()
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});
		},
		addOkay: function() {
			uni.showLoading({
				title: '加载中'
			})
			var params = {
				s: "App.Music.Search", // 必须，待请求的接口服务名称
				input: this.input,
				filter: this.filter,
				website: this.website,
				page: this.page,
			};
			uni.request({
				url: this.okayapiHost,
				method: 'GET',
				data: okayapi.enryptData(params),
				success: res => {
					this.music = this.music.concat(res.data.data.music)
					this.page++
					uni.hideLoading()
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});
		},
		bindPickerChange: function(e) {
			this.index = e.target.value
			this.website = this.array[this.index]['value']
			console.log(this.website)
		},
		onReachBottom() {
			this.addOkay();
			uni.showLoading({
				title: '加载中'
			})
		}
	}
}
